.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.operations {
  margin-bottom: 0; }
  .operations ul {
    list-style: none;
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    display: flex; }

